























var bitmap1=null;
import {
	mapState
} from 'vuex'

export default {
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	onShow() {
		var icon = plus.nativeObj.View.getViewById("icon");
		if (icon) {
			setTimeout(function(){
				icon.show();
			},100)
		}
	},
	onLoad: function(options) {
		bitmap1 = new plus.nativeObj.Bitmap('bmp1');
		bitmap1.load('static/img/logo.png',function(){
			console.log('bmp1.png load success!');
		},function(e){
			console.log('bmp1.png load failed! '+JSON.stringify(e));
		});
		this.createtab();
	},
	methods: {
		createtab: function() {
			console.log('App onLoad');
			// 设置水平居中位置
			var leftPos = Math.ceil((plus.screen.resolutionWidth - 60) / 2);
			var view = new plus.nativeObj.View('icon', {
				bottom: '11px',
				left:  leftPos+'px',
				width: '60px',
				height: '60px'
			});
			view.drawBitmap(bitmap1,
				{
					tag: 'rect',
					id: 'bg',
					position: {
						top: '1px',
						left: '0px',
						width: '100%',
						height: '100%'
					},
					rectStyles: {
						color: '#fff',
						radius: '50%',
						borderColor: '#ccc',
						borderWidth: '1px'
					}
				}, 
				{
					tag: 'rect',
					id: 'bg2',
					position: {
						bottom: '-0.5px',
						left: '0px',
						width: '100%',
						height: '45px'
					},
					rectStyles: {
						color: '#fff'
					}
				}, 
				{
					tag: 'rect',
					id: 'iconBg',
					position: {
						top: '5px',
						left: '5px',
						width: '50px',
						height: '50px'
					},
					rectStyles: {
						color: '#d74b28',
						radius: '50%'
					}
				}, 
				{
					tag: 'font',
					id: 'icon',
					//text: '\ue510', //此为字体图标Unicode码'\e600'转换为'\ue600'
					src: '../../static/img/logo.png',
					position: {
						top: '0px',
						left: '5px',
						width: '50px',
						height: '100%'
					}
				}
			);
			view.addEventListener("click", function(e) {
				uni.navigateTo({
					url: '../login/login'
				});
				var icon = plus.nativeObj.View.getViewById("icon");
				if (icon) {
					setTimeout(function(){
						icon.hide();
					},200)
				}
			}, false);
			view.show();
		}
	}
}



